import React, { Component } from "react";
import Info from "./Info";
import "./InfoPanel.css";

export default class InfoPanel extends Component {
    // <Info
    // title="Average interior size"
    // data="2,320"
    // sub="sq ft"
    // />
    getNumberOfBookmars = () => {
        if (!this.props.bookmarksSelected) return 0;
        if (!this.props.bookmarks) return 0;
        if (this.props.bookmarksSelected === "my") {
            if (
                !this.props.bookmarks.my_bookmarks ||
                this.props.bookmarks.my_bookmarks.length === 0
            )
                return 0;
            return this.props.bookmarks.my_bookmarks.length;
        }
        if (
            !this.props.bookmarks.shared ||
            this.props.bookmarks.shared.length === 0
        )
            return 0;
        for (const share of this.props.bookmarks.shared) {
            if (share.username === this.props.bookmarksSelected) {
                if (!share.bookmarks) return 0;
                return share.bookmarks.length;
            }
        }
        return 0;
    };

    render() {
        return (
            <div className="w-100 mt-lg-0 mt-md-0 mt-sm-3 mt-3">
                <Info
                    title="Total bookmarks"
                    data={this.getNumberOfBookmars()}
                />
            </div>
        );
    }
}
