import axios from "axios";
import { Promise } from "q";
import { API_URL, STATUS_OK } from "./../../config";

export default class LoginService {
    static login = (username, password) => {
        return new Promise((resolve, reject) => {
            axios
                .post(API_URL + "login", {
                    username,
                    password
                })
                .then(response => {
                    if (response.status === STATUS_OK) resolve(response.data);
                    else reject(response);
                })
                .catch(error => {
                    reject(error);
                });
        });
    };
}
