import React, { Component } from "react";
import Loading from "../../../Shared/Loading";
import "./Bookmark.css";
import defaultHouseImage from "./../../../../assets/image/house.png";

function formatPrice(price) {
    if (!price) return price;
    price = price.toLocaleString("en-CA");
    return `$${price}`;
}

export default class Bookmark extends Component {
    //     <div className="bookmark-thumbnail-circle">
    //     <div className="bookmark-thumbnail-circle-text">
    //         {this.props.circleText || "-"}
    //     </div>
    // </div>
    state = {
        isLoading: false
    };

    deleteBookmark = () => {
        this.setState({
            isLoading: true
        });
        this.props.deleteBookmark(
            this.props.bookmark.id,
            this.stopLoadingAnimation
        );
    };

    stopLoadingAnimation = () => {
        this.setState({
            isLoading: false
        });
    };

    goToLink = () => {
        window.open(this.props.bookmark.url, "_blank");
    };

    render() {
        return (
            <div className="bookmark-card m-lg-2 m-mb-2 m-sm-2 m-0">
                <button className="btn bookmark-body" onClick={this.goToLink}>
                    {this.state.isLoading ? (
                        <div className="bookmark-loading-bg position-absolute w-100 h-100 d-flex">
                            <Loading size="3" />
                        </div>
                    ) : (
                        ""
                    )}

                    <img
                        src={this.props.bookmark.img_url || defaultHouseImage}
                    />
                    <div className="bookmark-info">
                        <div className="bookmark-price">
                            {formatPrice(this.props.bookmark.price) ||
                                this.props.bookmark.title ||
                                "Preview not available"}
                        </div>
                        <div className="bookmark-address">
                            {this.props.bookmark.address ||
                                this.props.bookmark.description ||
                                "----"}
                        </div>
                        <div className="bookmark-url">
                            {this.props.bookmark.url}
                        </div>
                    </div>
                </button>
                <div className="d-flex justify-content-between">
                    <div className="bookmark-comments-text">
                        {this.props.numberOfComments || "0"} Comments
                    </div>
                    {!this.props.sharedBookmark ? (
                        <div className="dropup">
                            <button
                                className="btn btn-options"
                                id="dropdownMenuButton"
                                data-toggle="dropdown"
                                aria-haspopup="true"
                                aria-expanded="false"
                            >
                                <span className="fa fa-ellipsis-v"></span>
                            </button>

                            <div
                                className="dropdown-menu dropdown-menu-right"
                                aria-labelledby="dropdownMenuButton"
                            >
                                <button
                                    className="dropdown-item text-danger"
                                    onClick={this.deleteBookmark}
                                >
                                    <span className="fa fa-trash mr-2"></span>
                                    Delete bookmark
                                </button>
                            </div>
                        </div>
                    ) : (
                        ""
                    )}
                </div>
            </div>
        );
    }
}
