import React, { Component } from "react";

export default class Tab extends Component {
    render() {
        return (
            <li>
                <a
                    onClick={this.props.onClick}
                    className={
                        "align-items-center d-flex h-100 nav-link" +
                        (this.props.active ? " active" : "")
                    }
                    data-toggle="tab"
                    href={"#" + this.props.idKey}
                >
                    {this.props.children}
                </a>
            </li>
        );
    }
}
