import React, { Component } from "react";
import User from "./User";
import NewUserModal from "./NewUserModal";
import Popup from "reactjs-popup";
import "./UsersPanel.css";

export default class UsersPanel extends Component {
    renderUsers = () => {
        if (!this.props.usersShared || this.props.usersShared.length === 0)
            return;
        return this.props.usersShared.map(share => {
            return (
                <User
                    alert={this.props.alert}
                    key={share.receiver.username}
                    userInfo={this.props.userInfo}
                    share={share}
                    removeUserShared={this.props.removeUserShared}
                />
            );
        });
    };

    renderBtnAddUser = () => {
        return (
            <button className="btn btn-add-user align-self-start">
                <div className="user-card-img d-flex justify-content-center align-items-center">
                    <span className="fa fa-user-plus"></span>
                </div>
            </button>
        );
    };

    render() {
        return (
            <div className="col-lg-7 col-md-7 col-12 mt-lg-5 mt-md-5 mt-sm-3 mt-3">
                <div className="row mb-2 justify-content-lg-end justify-content-md-end justify-content-start">
                    <span className="badge badge-pill badge-primary">
                        {this.props.usersShared &&
                        this.props.usersShared.length > 0
                            ? "Sharing with:"
                            : ""}
                    </span>
                </div>
                <div className="row justify-content-lg-end justify-content-md-end justify-content-start">
                    {this.renderUsers()}
                    <Popup
                        contentStyle={{ width: "fit-content(500px)" }}
                        lockScroll={true}
                        className="animated fadeIn faster"
                        trigger={this.renderBtnAddUser()}
                        modal
                        closeOnDocumentClick
                        on="click"
                    >
                        {close => (
                            <NewUserModal
                                alert={this.props.alert}
                                userInfo={this.props.userInfo}
                                close={close}
                                addUserShared={this.props.addUserShared}
                            />
                        )}
                    </Popup>
                </div>
            </div>
        );
    }
}
