import React, { Component } from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import Board from "./components/Board";
import Login from "./components/Login";
import Signup from "./components/Signup";
import PageNotFound from "./components/PageNotFound";
import "./assets/css/bootstrap-4.3.1/css/bootstrap.min.css";
import { AuthConditionedRoute } from "./utils/Auth";
import { SnackbarProvider, wrapComponent } from "react-snackbar-alert";

function App() {
    const RouteredApp = wrapComponent(function({ createSnackbar }) {
        return (
            <BrowserRouter basename={process.env.PUBLIC_URL}>
                <Switch>
                    <AuthConditionedRoute
                        alert={createSnackbar}
                        path="/board"
                        shouldBeLogged={true}
                        pathIfFailedCondition="/login"
                        component={Board}
                    />
                    <AuthConditionedRoute
                        alert={createSnackbar}
                        path="/login"
                        shouldBeLogged={false}
                        pathIfFailedCondition="/login"
                        component={Login}
                    />
                    <AuthConditionedRoute
                        alert={createSnackbar}
                        shouldBeLogged={false}
                        path="/signup"
                        pathIfFailedCondition="/board"
                        component={Signup}
                    />
                    <Route component={PageNotFound} />
                </Switch>
            </BrowserRouter>
        );
    });

    return (
        <SnackbarProvider position="top">
            <RouteredApp />
        </SnackbarProvider>
    );
}

export default App;
