import axios from "axios";
import { Promise } from "q";
import { API_URL, STATUS_OK } from "./../../../../config";

export default class NewUserModalService {
    static shareBookmarks = (receiver_username, username, token) => {
        return new Promise((resolve, reject) => {
            axios
                .post(
                    `${API_URL}users/${username}/shares`,
                    {
                        receiver: { username: receiver_username }
                    },
                    {
                        headers: {
                            Authorization: `Bearer ${token}`
                        }
                    }
                )
                .then(response => {
                    if (response.status === STATUS_OK) resolve(response.data);
                    else reject(response);
                })
                .catch(error => {
                    reject(error);
                });
        });
    };
}
