import React, { Component } from "react";
import "./SearchPanel.css";

export default class SearchPanel extends Component {
    state = {
        searchText: "",
        showSearchText: undefined
    };

    onValueChange = e => {
        if (e.key === "Enter") {
            this.search();
        } else {
            this.setState({
                searchText: e.target.value
            });
        }
    };

    search = () => {
        if (this.state.searchText === "" && !this.state.showSearchText) return;
        this.props.onSearch(this.state.searchText);
        let showSearchText =
            this.state.searchText !== "" ? this.state.searchText : undefined;
        this.setState({
            showSearchText: showSearchText
        });
    };

    render() {
        return (
            <div className="d-flex flex-column col-lg-5 col-md-5 col-12 search-row mt-lg-5 mt-md-5 mt-sm-3 mt-3">
                <div className="row">
                    <input
                        className="search-input"
                        type="text"
                        onChange={this.onValueChange}
                        onKeyPress={this.onValueChange}
                    />
                    <button className="btn search-btn" onClick={this.search}>
                        <span className="fa fa-search"></span>
                    </button>
                </div>
                <div className="row">
                    {this.state.showSearchText ? (
                        <p className="badge">
                            Showing results for: "{this.state.showSearchText}"
                        </p>
                    ) : (
                        ""
                    )}
                </div>
            </div>
        );
    }
}
